/* SignUpPage.css   */

@keyframes fadeIn {
    from {
        opacity: 0;
        transform: translateY(20px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

.signuppage {
    /*  : iOS         body/   , 100vh  */
    height: 100vh;
    height: 100dvh;
    min-height: 100dvh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #FFFFFF;
    overflow: hidden; /*     */
}

.signuppage-container {
    display: flex;
    width: 30vw;
    height: 85vh;
    background-color: #ffffff;
    border-radius: 24px;
    overflow: hidden;
    padding: 75px;
    opacity: 0;
    animation: fadeIn 0.8s ease forwards;
    gap: 24px;
    min-width: 540px;
    
    /* PC      overflow: auto;  . */
    /*      . */
}

/*    */
.signuppage-section {
    flex: 1;
    display: flex;
    flex-direction: column;
    /* justify-content: center; */ /* PC     */
    padding: 80px;
    background-color: #ffffff;
    border-radius: 20px;
    box-shadow: 0 4px 16px rgba(0, 0, 0, 0.05);
    gap: 8px;
}

.signuppage-textarea h2 {
    font-size: 20px;
    font-weight: 800;
    color: #000000;
    margin-bottom: 6px;
    text-align: left;
    line-height: 1.4;
}

.signuppage-textarea p {
    font-size: 12px;
    font-weight: 400;
    color: #6b7280;
    margin-bottom: 32px;
    text-align: left;
}

.signuppage-formgroup {
    width: 100%;
    margin-bottom: 14px;
    position: relative;
}

.signuppage-formgroup input {
    width: 100%;
    padding: 12px 14px;
    font-size: 13px;
    border-radius: 8px;
    border: 1px solid transparent;
    background-color: #f9f9f9;
    box-sizing: border-box;
    transition: all 0.1s ease-in-out;
    color: #111827;
}

.signuppage-formgroup input::placeholder {
    color: #9CA3AF;
    font-size: 12px;
    font-weight: 500;
}

.signuppage-formgroup input:hover {
    background-color: #f3f4f6;
}

.signuppage-formgroup input:focus {
    outline: none;
    background-color: #ffffff;
    box-shadow: 0 0 0 2px #111827;
}

.signuppage-formgroup input.input-error {
    box-shadow: 0 0 0 2px #ef4444;
    background-color: #fff;
}

.signuppage-formgroup input.input-error:focus {
    box-shadow: 0 0 0 2px #ef4444;
    outline: none !important;
    background-color: #fff;
}

.signuppage-formgroup input.filled {
    border: 1px solid #e5e7eb;
    background-color: #ffffff;
}

.signuppage-formgroup .message-area {
    min-height: 16px;
    margin-top: 6px;
    margin-left: 5px;
    font-size: 12px;
    display: flex;
    align-items: center;
    gap: 6px;
    transition: all 0.2s ease-in-out;
}

/* ,  */
.signuppage-email {
    display: flex;
    gap: 24px;
    align-items: center;
}

.signuppage-email input {
    flex: 1;
    width: 100%;
    min-width: 0;
}

.signuppage-name {
    display: flex;
    gap: 24px;
    align-items: center;
}

.signuppage-name input {
    flex: 1;
    width: 100%;
    min-width: 0;
}

/* ,   */
.signuppage-checkbutton {
    padding: 12px 14px;
    background-color: #00CBA8;
    color: white;
    font-weight: 600;
    font-size: 12px;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    white-space: nowrap;
    transition: background-color 0.5s ease;
}

.signuppage-checkbutton:hover {
    background-color: #13BA9D;
}

/*   */
.signuppage-message.hidden {
    visibility: hidden;
}

.signuppage-error-message {
    display: flex;
    align-items: center;
    color: #ef4444;
    font-size: 12px;
    margin-top: 8px;
    margin-left: 5px;
    margin-bottom: 12px;
    gap: 6px;
    transition: none !important;
}

.signuppage-error-icon {
    font-size: 13px;
}

.signuppage-success-message {
    display: flex;
    align-items: center;
    color: #10b981;
    font-size: 12px;
    margin-top: 8px;
    margin-left: 5px;
    margin-bottom: 12px;
    gap: 6px;
    transition: none !important;
}

.signuppage-success-icon {
    font-size: 13px;
}

input.signuppage-error:focus {
    box-shadow: 0 0 0 2px #ef4444;
    outline: none !important;
    background-color: #ffffff;
}

.errorText {
    color: #ef4444;
    font-size: 12px;
    margin-top: 6px;
    display: block;
}

.signuppage-password {
    position: relative;
    width: 100%;
}

.signuppage-password input {
    width: 100%;
    padding-right: 40px;
}

.signuppage-password-icon {
    position: absolute;
    right: 12px;
    top: 55%;
    transform: translateY(-50%);
    cursor: pointer;
    color: #000000;
    font-size: 16px;
}

.signuppage-password-criteria {
    background-color: #f9f9f9;
    border-radius: 8px;
    padding: 12px 16px;
    margin-top: 8px;
    margin-bottom: 14px;
}

.signuppage-password-criterialist {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 6px 12px;
    padding: 0;
    margin: 0;
    list-style: none;
    padding: 4px;
    font-size: 12px;
}

.signuppage-password-criterialist li {
    display: flex;
    align-items: center;
    gap: 6px;
    color: #374151;
    font-weight: 600;
}

.signuppage-password-criterialist li.valid {
    color: #10b981;
}

.signuppage-password-criterialist li.invalid {
    color: #ef4444;
}

.signuppage-password-criterialist svg {
    font-size: 13px;
}


.signuppage-agreements-wrapper {
    border-radius: 6px;
    background-color: #f9f9f9;
    overflow: hidden;
    margin-bottom: 14px;
}

.signuppage-agreements-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 14px 16px;
    font-weight: 700;
    cursor: pointer;
    background-color: #f9f9f9;
    border: none;
    border-radius: 6px;
}

.signuppage-agreements-header:hover {
    background-color: #f3f4f6;
}

.agreements-title {
    font-size: 12px;
    color: #111827;
}

.agreements-right {
    display: flex;
    align-items: center;
    gap: 12px;
}

.check-all-text {
    font-size: 12px;
    font-weight: 500;
    color: #6b7280;
    cursor: pointer;
    transition: font-weight 0.5s ease, color 0.5s ease;
}

.check-all-text.active {
    font-weight: 700;
    color: #000000;
}

.arrow-icon {
    font-size: 18px;
    color: #6b7280;
}

.signuppage-agreements {
    display: flex;
    flex-direction: column;
    gap: 4px;
    padding: 8px 12px;
    background-color: #f9f9f9;
}

.signuppage-agreements .agreement-item {
    display: flex;
    align-items: center;
    gap: 10px;
    font-size: 11px;
    font-weight: 600;
    color: #111827;
}

.signuppage-agreements input[type="checkbox"] {
    appearance: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    width: 16px;
    height: 16px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #ffffff;
}

.signuppage-agreements input[type="checkbox"]:checked {
    background-color: #00CBA8;
    border: none;
    position: relative;
}

.signuppage-agreements input[type="checkbox"]:checked::after {
    content: "";
    color: white;
    font-size: 9px;
}

.signuppage-buttongroup {
    display: flex;
    gap: 20px;
    margin-top: 2rem;
    width: 100%;
}

.signuppage-buttongroup button {
    flex: 1;
    padding: 12px 0;
    border: none;
    border-radius: 6px;
    font-size: 14px;
    font-weight: 700;
    cursor: pointer;
    background-color: #00CBA8;
    color: #fff;
    transition: background-color 0.5s ease;
}

.signuppage-homebutton {
    background-color: #3a3a3a !important;
    color: #fff;
    transition: background-color 0.5s ease;
}

.signuppage-homebutton:hover {
    background-color: #000000 !important;
}

.signuppage-buttongroup button:hover {
    background-color: #13BA9D;
}

.signuppage-buttongroup button:disabled {
    background-color: #E8E9EC;
    color: #C0C0C0;
    cursor: not-allowed;
}

.signuppage-bottomtext {
    margin-top: 30px;
    font-size: 13px;
    color: #6B7280;
    text-align: center;
}

.signuppage-bottomtext-wrapper {
    text-align: center;
    font-size: 13px;
    color: #6B7280;
}

.signuppage-bottomtext-prompt {
    margin-right: 12px;
    cursor: default;
}

.signuppage-bottomtext-link {
    font-weight: 600;
    color: #000000;
    cursor: pointer;
    transition: color 0.2s ease;
}

.signuppage-bottomtext-link:hover {
    color: #555;
}

/* ------------------------------------------------ */
/*     (max-width: 768px)   */
/* ------------------------------------------------ */

@media screen and (max-width: 768px) {
    .signuppage {
        display: flex;
        justify-content: center;
        align-items: center;
        box-sizing: border-box;
        height: var(--app-height);
        min-height: var(--app-height);
        overflow: hidden;
    }

    .signuppage-container {
        width: 100%;
        min-width: 310px;
        height: auto;
        min-height: auto;
        border-radius: 0;
        box-shadow: none;
        display: flex;
        flex-direction: column;
        justify-content: center;
        overflow-y: auto; 
        -webkit-overflow-scrolling: touch; 
        padding: 40px 16px 0px 16px;
    }

    .signuppage-section {
        padding: 0px 24px 0px 24px !important;
        border-radius: 12px;
        box-shadow: none;
        min-height: calc(100% - 60px); 
    }

    .signuppage-section form {
        margin-bottom: 60px; 
    }

    .signuppage-textarea h2 {
        font-size: 14px;
    }

    .signuppage-textarea p {
        font-size: 10px;
        margin-bottom: 20px;
    }

    .signuppage-formgroup {
        margin-bottom: 4px;
    }

    .signuppage-formgroup input {
        font-size: 10px;
        padding: 10px 12px;
    }

    .signuppage-formgroup input::placeholder {
        font-size: 10px;
    }

    .signuppage-checkbutton {
        font-size: 10px;
        padding: 10px 12px;
    }

    .signuppage-password-icon {
        font-size: 14px;
    }

    .signuppage-password-criteria {
        padding: 8px 12px;
    }

    .signuppage-password-criterialist {
        grid-template-columns: repeat(2, 1fr);
        font-size: 10px;
    }

    .agreements-title {
        font-size: 10px;
    }

    .check-all-text {
        font-size: 10px;
    }

    .signuppage-agreements .agreement-item {
        font-size: 10px;
    }

    .signuppage-buttongroup {
        flex-direction: row;
        gap: 12px;
        margin-top: 20px;
    }

    .signuppage-buttongroup button {
        font-size: 12px;
        padding: 10px 0;
    }

    .signuppage-bottomtext {
        margin-top: 20px;
        font-size: 11px;
    }

    .signuppage-bottomtext-wrapper {
        font-size: 11px;
    }

    .signuppage-bottomtext-link {
        font-size: 11px;
    }

    .signuppage-error-message {
        margin-top: 4px;
        margin-bottom: 8px;
        font-size: 10px;
    }

    .signuppage-error-icon {
        font-size: 10px;
    }

    .signuppage-success-message {
        margin-top: 4px;
        margin-bottom: 8px;
        font-size: 10px;
    }

    .signuppage-success-icon {
        font-size: 10px;
    }
}