/*  */
.cts-wrapper{
  position: relative;
  display: block;        
  width: 100%;
  overflow: visible;
}

/* () -    & /  */
.cts-field{
  width: 100%;
  height: 100%;
  display: flex;             
  align-items: center;
  justify-content: center;
  padding: 0 12px;
  border: 0;
  outline: none;
  border-radius: 8px;
  background-color: var(--color-main2);
  font-size: 14px;
  font-weight: 500;
  line-height: 1;
  text-align: center;
  box-sizing: border-box;
  -webkit-appearance: none;
  appearance: none;
  cursor: pointer;
}

.cts-disabled{ 
  background: var(--color-disable);
  color: var(--color-disable-text);
  cursor: not-allowed; 
}

/*  ( ) */
.cts-caret{
  position: absolute;
  right: 12px;
  top: 50%;
  transform: translateY(-50%);
  pointer-events: none;
  font-size: 14px;
  color: var(--color-text);
}

/* : /(top/left) JS style  */
.cts-popover{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 8px;
  padding: 8px;
  width: 320px;
  background-color: var(--color-main2);
  border: none;
  border-radius: 10px;
  box-shadow: 0 8px 24px rgba(0,0,0,0.08);
}

/*   */
.cts-column{
  max-height: 220px;
  overflow-y: auto;
  border-radius: 8px;
  background-color: var(--color-main1);
  padding: 8px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
}

/*   */
.cts-option{
  display: block;
  width: 100%;
  padding: 10px 10px;
  border: none;
  background: transparent;
  border-radius: 8px;
  text-align: center;
  font-size: 12px;
  font-weight: 600;
  cursor: pointer;
}

.cts-option:hover{ 
  background-color: var(--color-main1-hover);
}

.cts-option.selected{
  background-color: var(--color-selected);
  color: var(--color-text);
  font-weight: 700;
}

